.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.nav-container{

  width: 100%;

  background-color: black;
  color: aqua;
}

.logo{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}

.nav-btn-container{
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav-btn{
  display: flex;
  justify-content: flex-end;
  font-size: 0.8rem;
  margin-right: 10px;
  background-color: black;
}

.modal-container{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 11rem;
  height: 15rem;
  background-color:hotpink;
  color:#282c34;
}

.modal-title{
  display: flex;
  justify-content: flex-end;
}

.footer-container{
  background-color: black;
  color:aqua;
}




@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
